import { Component, Vue } from 'vue-property-decorator'
const namespace: string = 'oidcStore/';

 @Component({
   computed: {
      ...mapGetters('oidcStore', [
                'oidcIsAuthenticated',
                'oidcUser'
            ])
    },
    methods: {
      ...mapActions('oidcStore', ['authenticateOidcSilent',
                'signOutOidc', 'authenticateOidc'])
    },
})

